export default{
	data(){
		return{
			playState:false,
		}
	},
	props:{
		currentLyric:{
			type:Object
		},
		windowHeight:{
			type:String
		}
	},
	created() {
		console.log("qqqqqqqq")
	},
	methods:{
		//返回上一层
		backUpPage(){
			this.$emit('toSongDetail',false)
		},
		//上一首
		switchUp(){
			this.playState=true
		},
		//下一首
		switchNext(){
			this.playState=true
		},
		//暂停播放切换
		switchPlay(){
			this.playState=!this.playState
		}
	}
}
